import Vue from "vue";
import Vuex from "vuex";
import api from "@/utils/api";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    comments: [],
  },
  mutations: {
    GET_COMMENTS(state, comments) {
      Vue.set(state, "comments", comments);
    },

    REMOVE_COMMENT(state, payload) {
      state.comments.forEach(comment => {
        if (comment.id === payload.comment_id) {
          comment.isDeleted = true;
          comment.dateModified = new Date;
        }
      });
    },

    SET_SERVICE(state, service) {
      Vue.set(state, "service", service);
    },
  },
  actions: {
    async fetchComments({commit}, payload) {
      try {
        const {data} = await api.get(`/${payload}/comments/1/`, {
          params: {
            presentation: "tree",
            scope: ["all"],
            // parent_id: 0,
          },
        });
        commit("GET_COMMENTS", data);
        return data;
      } catch (e) {
        console.log(e);
      }
    },

    async SET_COMMENT({dispatch}, payload) {
      try {
        await api.post(`/${payload.service_id}/comments/1/`, {
          comment_text: payload.comment_text,
          user: {
            external_id: payload.user.external_id,
            first_name: payload.user.first_name,
            last_name: payload.user.last_name,
            user_group: payload.user.user_group,
          },
          parent_id: payload.parent_id > 0 ? payload.parent_id : null,
          options: {
            scope: payload.options.scope,
          },
          signature: payload.signature,
        });
        dispatch("fetchComments", `${payload.service_id}`);
      } catch (e) {
        console.log(e);
      }
    },

    async DEL_COMMENT({commit, dispatch}, payload) {
      try {
        await api.delete(`/${payload.service_id}/${payload.data_type}/${payload.item_id}/${payload.comment_id}/`);
        commit("REMOVE_COMMENT", payload);
        dispatch("fetchComments", `${payload.service_id}`);
      } catch (e) {
        console.log(e);
      }
    },

    async EDIT_COMMENT({dispatch}, payload) {
      try {
        await api.put(`/${payload.service_id}/comments/1/${payload.id}/`, {
          comment_text: payload.comment_text,
          scope: payload.scope,
        });
        dispatch("fetchComments", `${payload.service_id}`);
      } catch (e) {
        console.log(e);
      }
    },
  },
  getters: {
    COMMENTS: state => state.comments,
  },
});
